import {api} from "../../utils/api"
const  state={
    product:[],
};
const mutations={
    Product(state,data){
        state.product=data;
    }
};
const actions={
    getProduct(context,data){
      return api.get(`/productDetail?${data}`).then(response=>{
            context.commit("Product",response);
        })

    }
};
export  default{
    namespaced:true,
    state,mutations,actions
}