<template>
    <div>
    <app-header id="top" ></app-header>
    <div class="renderContent">
        <div class="hero">
            <div class="container hero-container">
                <div class="hero-banner">
                    <div class="banner-ui">
                        <router-link :to="{path:'/product',query:{pid:(value.url.split('?')[1]).split('=')[1]}}" v-for="(value,index) in carousel" :key="index" :title="value.name"  :class="{show:index==index1}"><img :src="value.img"/></router-link>
                    </div>
                    <div class="banner-control">
                        <div class="control-page">
                            <a v-for="(value,index) in carousel" :key="index" @click="index1=index" :class="{active:index==index1}" ></a>
                        </div>
                        <div class="control-rl">
                            <a href="#" class="rl-prev" @click="changeShow(-1)">上一张</a>
                            <a href="#" class="rl-next" @click="changeShow(1)">下一张</a>
                        </div>
                    </div>
                </div>
                <div class="hero-sub clearfix">
                    <div class="sub-tool">
                        <ul class="clearfix">
                            <li><a href="#"><i class="iconfont">&#xe908;</i>选购手机</a></li>
                            <li><a href="#"><i class="iconfont">&#xe905;</i>企业团购</a></li>
                            <li><a href="#"><i class="iconfont">&#xe902;</i>F码通道</a></li>
                            <li><a href="#"><i class="iconfont">&#xe906;</i>米粉卡</a></li>
                            <li><a href="#"><i class="iconfont">&#xe904;</i>以旧换新</a></li>
                            <li><a href="#"><i class="iconfont">&#xe907;</i>话费充值</a></li>
                        </ul>
                    </div>
                    <div class="sub-promo">
                        <ul>
                            <li class="first"><a href="#"><img src="../img/promo1.jpg" alt=""></a></li>
                            <li><a href="#"><img src="../img/promo2.jpg" alt=""></a></li>
                            <li><a href="#"><img src="../img/promo3.jpg" alt=""></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>

        <div class="main">
            <div class="container">
                <div v-for="(value,index) in mainData">
                    <div class="main-hd"  >
                        <h2>{{value.name}}</h2>
                    </div>
                    <div class="main-bd clearfix" >
                        <div class="bd-ad" >
                            <ul class="clearfix">
                                <li><a href="#"><img :src="value.img" alt=""></a></li>
                            </ul>
                        </div>
                        <div class="bd-product clearfix">
                            <ul class="clearfix">
                                <li class="tpl-body-item" v-for="(value,index) in value.list" :key="index">
                                    <div>
                                        <div class="face"><a href="#"><img :src="value.img" alt=""></a></div>
                                        <h3 class="title"><a href="#">{{value.name}}</a></h3>
                                        <p class="desc">{{value.desc}}</p>
                                        <p class="price">
                                            <span class="num">{{value.price}}</span>元
                                            <del><span class="num">{{value.market_price}}</span>元</del>
                                        </p>
                                        <div class="flag" :style="{backgroundColor:value.flagcolor}">{{value.flag}}</div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
     <div class="scrollTop" v-if="ScrollShow" @click="backToTOP">
         <img src="../img/scrollTop.png">
         <div>返回到顶部</div>
     </div>
    </div>
</template>
<script>
    import Vue from "vue"
    import {mapState} from "vuex";
    import appHeader from "../components/Miheader.vue"
    export default{
        created(){
            document.title='主页'
            if(this.carousel.length===0||this.mainData.length===0){
                this.$store.dispatch("render/getCarousel");
                this.$store.dispatch("render/getMainData");
            }
            this.start()

             this.$store.dispatch("render/getIsHome",true);
        },
        mounted(){
          window.addEventListener("scroll",this.scroll)
        },
        destroyed(){
            window.removeEventListener('scroll',this.scroll)
        },
        components:{
            appHeader ,
        },
        data(){
            return{
                index1:0,
                timer:0,
                loading:true,
                loadingServe:'',
                timer2:'',
                ScrollShow:false
            }
        },
        computed:{
            ...mapState("render",["carousel","mainData"]),
            changeData(){
                let {carousel,mainData}=this;
                return{
                    carousel,mainData
                }
            }
        },
        methods: {
            changeShow(index){
                let len = this.carousel.length;
                this.index1 = (this.index1 + index + len) % len;

            },
            start(){
                let that=this;
                that.timer = setInterval(function () {
                    that.changeShow(1);
                }, 3000);
            },
            scroll(){
                const scrollTop=window.pageYOffset || document.body.scrollTop || document.body.scrollTop;
                const activeNum=scrollTop/document.body.scrollHeight;
                if(activeNum>0.4&&this.ScrollShow===false){
                    this.ScrollShow=true;
                }else if(activeNum<0.4&&this.ScrollShow===true){
                    this.ScrollShow=false;
                }
            },
            backToTOP(){
                document.documentElement.scrollTop=0
                document.body.scrollTop=0;
//               let timer3 = setInterval(function () {
//                let osTop = document.documentElement.scrollTop || document.body.scrollTop ||window.pageYOffset;
//                let ispeed = Math.floor(osTop / 5)
//                document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
//                if (osTop === 0) {
//                    clearInterval(timer3)
//                }
//               }, 16)
            }

        },
        watch:{
            index1:function () {
                if(this.index1===4){
                clearInterval(this.timer);
                this.timer='';
                this.start()
                }
            },
            timer:function () {
//                console.log(this.timer)
            },
//            changeData:function () {
//                if(this.carousel.length>0&&this.mainData.length>0){
//                    const that=this;
//                       this.timer2=setTimeout(()=>{
//                           that.loadingServe.close();
//                           Vue.$message({
//                               message: '加载成功',
//                               type: 'success'
//                           });
//                           clearTimeout(that.timer2)
//                       },100)
//                }
//            }
        }
    }
</script>
<style lang="scss" scoped>
    @import "../css/style.css";
    @import "../css/index.css";
    @import "../css/iconfont.css";

    .scrollTop{
            position: fixed;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            right:0px;
            top:50%;
            transform: translateY(-50%);
            background-color: #f5f5f5;
            border-radius: 5px;
            /*border: 0.5px solid #c8c8ce;*/
            z-index: 99;
            color: #7a7a7a;
            cursor: pointer;
            opacity: 0.3;
            transition: all 0.3s linear;
            img{
                width: 48px;
                height: 48px;
            }
        }
    .scrollTop:hover{
        opacity: 1;
    }
</style>