<template>
    <div>
        <div class="topbar">
            <div class="container">
                <div class="topbar-nav">
                    <a href="">小米商城</a><span>|</span>
                    <a href="">MIUI</a><span>|</span>
                    <a href="">IoT</a><span>|</span>
                    <a href="">云服务</a><span>|</span>
                    <a href="">金融</a><span>|</span>
                    <a href="">有品</a><span>|</span>
                    <a href="">小爱开放平台</a><span>|</span>
                    <a href="">政企服务</a><span>|</span>
                    <a href="">Select Region</a>
                </div>
                <div class="topbar-cart logined" v-show="isLogin">
                    <router-link to="/cart" @click.native="toCart"><i class="iconfont">&#xe60c;</i>购物车<span>（<label class="count">{{ShopCartNum}}</label>）</span></router-link>
                </div>
                <div class="topbar-info clearfix" >
                    <template v-if="!isLogin">
                    <router-link to="/login" class="not-logined" @click.native="loginAc(true)">登录</router-link>
                    <span class="not-logined">|</span>
                    <router-link to="/login" class="not-logined" @click.native="loginAc(false)">注册</router-link>
                    <span class="not-logined">|</span>
                    </template>
                    <a class="logined username" v-show="isLogin">{{user.name}}</a><span class="logined">|</span>
                    <a class="sep">消息通知</a>
                    <a href="#" class="sep logined logout"  v-show="isLogin" @click="logout">登出</a>
                </div>
            </div>
        </div>
        <div class="header">
            <div class="container">
                <div class="header-logo">
                    <router-link to="/" type="a" replace></router-link>
                </div>
                <div class="header-nav">
                    <ul class="nav-list clearfix">
                        <li class="nav-category" @mouseenter="enter" @mouseleave="leave">
                            <a href="javascript:;">全部商品分类</a>
                            <div class="category-list" v-if="isShow">
                                <ul :class="[isHome?'defaultCategoryList':'otherPageCateGoryList']">
                                    <li class="tpl-cat" v-for="(value,index) in leftMenuList" :key="index">
                                        <a class="title" :class="[isHome?'defaultTitle':'otherPageTitle']">
                                            <span>{{value.name}}</span>
                                            <i class="iconfont"></i>
                                        </a>
                                        <div class="children clearfix">
                                            <ul class="children-list">
                                                <li class="tpl-children" v-for="(value,index) in value.list" :key="index">
                                                    <a href="#">
                                                        <img :src="value.img" alt="" />
                                                        <span class="text">{{value.name}}</span>
                                                    </a>
                                                </li>
                                            </ul>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </li>
                        <li class="nav-item tpl-nav" v-for="(value,index) in menuList" :key="index">
                            <a>{{value.name}}</a>
                        </li>
                    </ul>
                </div>
                <div class="header-search">
                    <form action="" class="search-form">
                        <input type="search" name="keyword" class="search-text">
                        <button type="submit"  class="search-btn iconfont">&#xe616</button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {api} from "../utils/api"
import {mapState} from "vuex";
import Vue from "vue"
export default{
    created(){
        if(this.isHome){
            this.isShow=true;
        }else{
            this.isShow=false;
            console.log(this.isShow)
        }
    },
    data(){
        return{
            itemList:[],
            tplChildren:[],
            isShow:true
        }
    },
    computed:{
        ...mapState("render",["menuList","leftMenuList",'isHome']),
        ...mapState("userMs",["isLoginEnter","isLogin","user","ShopCartNum"]),
    },
    watch:{
        isHome:function () {
            if(this.isHome){
                this.isShow=true;
            }else{
                this.isShow=false;
                console.log(this.isShow)
            }
        }
    },
    methods:{
          loginAc(flag){
              this.$store.dispatch("userMs/loginEnter",flag);
          },
        logout(){
            this.$store.dispatch("userMs/logout");
            Vue.$message({
                type:"success",
                message:"已退出登陆"
            })
        },
        enter(){
          this.isShow=true
        },
        leave(){
            if(this.isHome){
                this.isShow=true;
            }else {
                this.isShow=false;
            }
        },
        toCart(){
            this.$store.dispatch("render/getIsHome",false);
        }
    }
}
</script>
<style lang="scss">
    @import "../css/style.css";
    @import "../css/index.css";
    @import "../css/iconfont.css";
    .defaultCategoryList{
        background: rgba(0,0,0,0.6);

    }
    .otherPageCateGoryList{
        background: #fff;
        border: 1px solid #ff6700;

    }
    .defaultTitle{
        color: white;
    }
    .otherPageTitle{
        color: #424242;
    }
</style>